import React, { PropTypes } from 'react';
import withStyles from 'isomorphic-style-loader/lib/withStyles';
import s from './Verifyproviderlogin.css';
import me from '../../data/queries/me.js';

const title = 'Verify Credential';

function Verifyproviderlogin(props, context) {
 
  context.setTitle(title);
  
  return (
    <div className={s.root}>
      <div className={s.container}>
        <h1>{title}</h1>
        <p>Password Verified</p>
                     
      </div>
    </div>
  );
}

Verifyproviderlogin.contextTypes = { setTitle: PropTypes.func.isRequired };

export default withStyles(s)(Verifyproviderlogin);



